<template>
  <view class="fahui-page">
    <image class="fahui-banner" :src="bannerImage" mode="aspectFill" v-if="bannerImage" />
    <view class="banner-desc" v-if="bannerDesc">{{ bannerDesc }}</view>

    <!-- ÂõæÊñá‰ªãÁªç -->
    <view class="intro-section">
      <view v-if="specialIntro.image" class="intro-image">
        <image :src="specialIntro.image" mode="aspectFill" class="intro-img" />
      </view>
      <view class="intro-content">{{ specialIntro.content }}</view>
    </view>

    <!-- Êä•Âêç‰∫∫‰ø°ÊÅØ -->
    <view class="section">
      <view class="section-title">
        <text class="icon">üë•</text>Êä•Âêç‰∫∫‰ø°ÊÅØ
        <text class="section-tip">ÔºàÊúÄÂ§ö5‰∫∫ÔºåÊØè‰∫∫‰ø°ÊÅØÈúÄÂÆåÊï¥Ôºâ</text>
      </view>
      
      <ParticipantForm 
        :value="applicants"
        @change="onParticipantsChange"
      />
    </view>

    <!-- Ê≥ï‰ºöÈ°πÁõÆÈÄâÊã© -->
    <view class="section">
      <view class="section-title">
        <text class="icon">üïØÔ∏è</text>Ê≥ï‰ºöÈ°πÁõÆ
        <text class="section-tip">ÔºàÂøÖÈÄâÔºâ</text>
      </view>
      <view class="project-list">
        <view v-for="(project, index) in fahuiProjects" :key="index" class="project-item" @click="selectProject(index)">
          <view class="project-info">
            <text class="project-name">{{ project.name }}</text>
            <text class="info-icon" @click.stop="showProjectIntro(project)">‚ÑπÔ∏è</text>
            <text class="project-desc">{{ project.description }}</text>
            <text class="project-price">¬•{{ project.price }}</text>
            <text class="project-remaining">Ââ©‰ΩôÂêçÈ¢ùÔºö{{ project.maxApplicants ? (project.maxApplicants - (project.currentApplicants || 0)) : '‰∏çÈôê' }}</text>
          </view>
          <view class="project-status" :class="{ active: selectedProjectIndex === index }">
            <text class="status-icon">{{ selectedProjectIndex === index ? '‚úì' : '‚óã' }}</text>
          </view>
        </view>
      </view>
    </view>

    <!-- ÈÖçÂÅ∂‰ø°ÊÅØÔºàÂßªÁºòÂíåÂêàÊ≥ï‰ºöÔºâ -->
    <view class="section" v-if="selectedProjectIndex === 0">
      <view class="section-title">
        <text class="icon">üíï</text>ÈÖçÂÅ∂‰ø°ÊÅØ
        <text class="section-tip">ÔºàÈÄâÂ°´Ôºâ</text>
      </view>
      <view class="spouse-form">
        <view class="form-group">
          <text class="form-label">ÈÖçÂÅ∂ÂßìÂêç</text>
          <input v-model="spouse.name" placeholder="ËØ∑ËæìÂÖ•ÈÖçÂÅ∂ÂßìÂêç" class="form-input" maxlength="10" />
        </view>
        
        <view class="form-group">
          <text class="form-label">ÊÄßÂà´</text>
          <picker :range="genderOptions" @change="onSpouseGenderChange" :value="genderOptions.indexOf(spouse.gender)">
            <view class="picker">
              <text class="picker-value">{{ spouse.gender || 'ËØ∑ÈÄâÊã©ÊÄßÂà´' }}</text>
              <text class="picker-arrow">‚ñº</text>
            </view>
          </picker>
        </view>
        
        <view class="form-group">
          <text class="form-label">ËÅîÁ≥ªÁîµËØù</text>
          <input v-model="spouse.phone" placeholder="ËØ∑ËæìÂÖ•ËÅîÁ≥ªÁîµËØù" class="form-input" type="number" maxlength="11" />
        </view>
        
        <view class="form-group">
          <text class="form-label">ÂÜúÂéÜÁîüÊó•</text>
          <picker mode="date" @change="onSpouseBirthdayChange" :value="spouse.lunarBirthday">
            <view class="picker">
              <text class="picker-value">{{ spouse.lunarBirthday || 'ËØ∑ÈÄâÊã©ÂÜúÂéÜÁîüÊó•' }}</text>
              <text class="picker-arrow">‚ñº</text>
            </view>
          </picker>
        </view>
        
        <view class="form-group">
          <text class="form-label">Âá∫ÁîüÊó∂Ëæ∞</text>
          <picker :range="birthTimeOptions" @change="onSpouseBirthTimeChange" :value="birthTimeOptions.indexOf(spouse.birthTime)">
            <view class="picker">
              <text class="picker-value">{{ spouse.birthTime || 'ËØ∑ÈÄâÊã©Âá∫ÁîüÊó∂Ëæ∞' }}</text>
              <text class="picker-arrow">‚ñº</text>
            </view>
          </picker>
        </view>
        
        <view class="form-group">
          <text class="form-label">Â∏∏‰ΩèÂú∞ÂùÄ</text>
          <input v-model="spouse.address" placeholder="ËØ∑ËæìÂÖ•Â∏∏‰ΩèÂú∞ÂùÄ" class="form-input" />
        </view>
      </view>
    </view>

    <!-- Ë∂ÖÂ∫¶‰ø°ÊÅØÔºàË∂ÖÂ∫¶Ê≥ï‰ºöÔºâ -->
    <view class="section" v-if="selectedProjectIndex === 1">
      <view class="section-title">
        <text class="icon">üïØÔ∏è</text>Ë∂ÖÂ∫¶‰ø°ÊÅØ
        <text class="section-tip">ÔºàÂøÖÂ°´Ôºâ</text>
      </view>
      
      <view class="form-group">
        <text class="form-label">Ë∂ÖÂ∫¶Á±ªÂûã <text class="required">*</text></text>
        <picker :range="chaoduTypes" @change="onChaoduTypeChange" :value="chaoduTypes.indexOf(chaoduType)">
          <view class="picker">
            <text class="picker-value">{{ chaoduType || 'ËØ∑ÈÄâÊã©Ë∂ÖÂ∫¶Á±ªÂûã' }}</text>
            <text class="picker-arrow">‚ñº</text>
          </view>
        </picker>
      </view>
      
      <view class="deceased-list">
        <view v-for="(deceased, index) in deceasedList" :key="index" class="deceased-card">
          <view class="deceased-header">
            <text class="deceased-title">Â∑≤ÊïÖ‰∫≤‰∫∫ {{ index + 1 }}</text>
            <button v-if="deceasedList.length > 1" class="remove-btn" @click="removeDeceased(index)">Âà†Èô§</button>
          </view>
          
          <view class="form-group">
            <text class="form-label">ÂßìÂêç <text class="required">*</text></text>
            <input v-model="deceased.name" placeholder="ËØ∑ËæìÂÖ•ÂßìÂêç" class="form-input" maxlength="10" />
          </view>
          
          <view class="form-group">
            <text class="form-label">ÂÖ≥Á≥ª <text class="required">*</text></text>
            <picker :range="relationOptions" @change="onRelationChange(index, $event)" :value="relationOptions.indexOf(deceased.relation)">
              <view class="picker">
                <text class="picker-value">{{ deceased.relation || 'ËØ∑ÈÄâÊã©ÂÖ≥Á≥ª' }}</text>
                <text class="picker-arrow">‚ñº</text>
              </view>
            </picker>
          </view>
          
          <view class="form-group">
            <text class="form-label">Âá∫ÁîüÂÜúÂéÜ</text>
            <picker mode="date" @change="onDeceasedBirthdayChange(index, $event)" :value="deceased.lunarBirthday">
              <view class="picker">
                <text class="picker-value">{{ deceased.lunarBirthday || 'ËØ∑ÈÄâÊã©Âá∫ÁîüÂÜúÂéÜ' }}</text>
                <text class="picker-arrow">‚ñº</text>
              </view>
            </picker>
          </view>
          
          <view class="form-group">
            <text class="form-label">Âéª‰∏ñÂÜúÂéÜ</text>
            <picker mode="date" @change="onDeceasedDeathChange(index, $event)" :value="deceased.deathDate">
              <view class="picker">
                <text class="picker-value">{{ deceased.deathDate || 'ËØ∑ÈÄâÊã©Âéª‰∏ñÂÜúÂéÜ' }}</text>
                <text class="picker-arrow">‚ñº</text>
              </view>
            </picker>
          </view>
        </view>
      </view>
      
      <button v-if="deceasedList.length < 5" class="add-btn" @click="addDeceased">
        <text class="add-icon">+</text>
        <text class="add-text">Ê∑ªÂä†Â∑≤ÊïÖ‰∫≤‰∫∫</text>
      </button>
    </view>

    <!-- ‰ª£ÂäûÁâ©ÂìÅ -->
    <view class="section" v-if="showGoods">
      <view class="section-title">
        <text class="icon">üõçÔ∏è</text>‰ª£ÂäûÁâ©ÂìÅ
        <text class="section-tip">ÔºàÂèØÈÄâÔºâ</text>
      </view>
      <view class="goods-list">
        <view v-for="(item, index) in goodsList" :key="index" class="goods-item">
          <view class="goods-info">
            <text class="goods-name">{{ item.name }}</text>
            <text class="goods-desc">{{ item.description }}</text>
            <text class="goods-price">¬•{{ item.price }}</text>
          </view>
          <view class="goods-control">
            <button class="qty-btn" @click="decreaseGoodsQty(index)" :disabled="item.qty <= 0">-</button>
            <text class="qty-value">{{ item.qty }}</text>
            <button class="qty-btn" @click="increaseGoodsQty(index)">+</button>
          </view>
        </view>
      </view>
      <view class="total-section">
        <text class="total-label">‰ª£ÂäûË¥πÁî®Ôºö</text>
        <text class="total-price">¬•{{ goodsTotalPrice }}</text>
      </view>
    </view>

    <!-- Êî∂‰ª∂‰ø°ÊÅØ -->
    <view class="section" v-if="showReceiver">
      <view class="section-title">
        <text class="icon">üì¶</text>Êî∂‰ª∂‰ø°ÊÅØ
        <text class="section-tip">ÔºàÂèØÈÄâÔºâ</text>
      </view>
      <view class="receiver-form">
        <view class="form-group">
          <text class="form-label">Êî∂‰ª∂‰∫∫</text>
          <input v-model="receiver.name" placeholder="ËØ∑ËæìÂÖ•Êî∂‰ª∂‰∫∫" class="form-input" maxlength="10" />
        </view>
        
        <view class="form-group">
          <text class="form-label">ËÅîÁ≥ªÁîµËØù</text>
          <input v-model="receiver.phone" placeholder="ËØ∑ËæìÂÖ•ËÅîÁ≥ªÁîµËØù" class="form-input" type="number" maxlength="11" />
        </view>
        
        <view class="form-group">
          <text class="form-label">Êî∂‰ª∂Âú∞ÂùÄ</text>
          <input v-model="receiver.address" placeholder="ËØ∑ËæìÂÖ•Êî∂‰ª∂Âú∞ÂùÄ" class="form-input" />
        </view>
      </view>
    </view>

    <!-- ‰∏ã‰∏ÄÊ≠•ÊåâÈíÆ -->
    <view class="footer">
      <button class="next-btn" @click="nextStep">‰∏ã‰∏ÄÊ≠•</button>
    </view>

    <view v-if="showHistoryDialog" class="history-dialog-mask" @click="showHistoryDialog=false">
      <view class="history-dialog" @click.stop tabindex="0" @keydown="onHistoryKeydown">
        <view class="dialog-title">ÈÄâÊã©ÂéÜÂè≤Êä•Âêç‰∫∫</view>
        <view class="search-bar">
          <input v-model="historySearch" class="search-input" placeholder="ÊêúÁ¥¢ÂßìÂêçÊàñÁîµËØù" @input="onHistorySearch" />
          <text v-if="historySearch" class="search-clear" @click="clearHistorySearch">√ó</text>
        </view>
        <view v-if="filteredHistory.length === 0" class="empty-tip">
          <text class="empty-icon">üóÇÔ∏è</text> ÊöÇÊó†ÂåπÈÖçÊä•Âêç‰∫∫
        </view>
        <view v-else class="history-list">
          <view v-for="(item, idx) in filteredHistory" :key="idx" class="history-item" :class="{hover: hoverIdx===idx, selected: batchDeleteSet.has(idx)}"
            @mouseenter="hoverIdx=idx" @mouseleave="hoverIdx=-1" @click="onHistoryItemClick(idx, item)">
            <view class="history-main">
              <view class="history-name">{{ item.name }} <text class="history-phone">{{ item.phone }}</text></view>
              <view class="history-detail">{{ item.gender }} | {{ item.lunarBirthday }} | {{ item.birthTime }} | {{ item.address }}</view>
              <view class="history-detail2">Ë∫´‰ªΩËØÅÂè∑Ôºö{{ item.idCard || '‚Äî' }}</view>
            </view>
            <button class="history-del" v-if="!batchDeleteMode" @click.stop="deleteHistoryParticipant(idx)">Âà†Èô§</button>
            <input v-if="batchDeleteMode" type="checkbox" class="batch-checkbox" :checked="batchDeleteSet.has(idx)" @change.stop="toggleBatchDelete(idx)" />
          </view>
        </view>
        <view class="dialog-actions">
          <button class="dialog-close" @click="showHistoryDialog=false">ÂÖ≥Èó≠</button>
          <button class="dialog-btn" @click="toggleBatchDeleteMode">{{ batchDeleteMode ? 'ÂèñÊ∂àÊâπÈáè' : 'ÊâπÈáèÂà†Èô§' }}</button>
          <button class="dialog-btn" @click="exportHistory">ÂØºÂá∫</button>
          <button class="dialog-btn" @click="importHistory">ÂØºÂÖ•</button>
          <button class="dialog-btn danger" v-if="batchDeleteMode && batchDeleteSet.size" @click="confirmBatchDelete">Á°ÆËÆ§Âà†Èô§({{ batchDeleteSet.size }})</button>
        </view>
        <input v-if="showImportInput" type="file" accept="application/json" style="display:none" ref="importFile" @change="onImportFile" />
      </view>
    </view>
  </view>
</template>

<script>
import ParticipantForm from '@/components/ParticipantForm.vue'

export default {
  components: {
    ParticipantForm
  },
  data() {
    return {
      bannerImage: '',
      bannerDesc: '',
      specialIntro: { content: '', image: '' },
      applicants: [this.getEmptyApplicant()],

      fahuiProjects: [], // Êîπ‰∏∫Âä®ÊÄÅËé∑Âèñ
      selectedProjectIndex: -1,
      spouse: {
        name: '',
        gender: '',
        phone: '',
        lunarBirthday: '',
        birthTime: '',
        address: ''
      },
      chaoduTypes: ['Ë∂ÖÂ∫¶Ê≥ï‰ºö', 'Ë∂ÖÂ∫¶Áâå‰Ωç', 'Ë∂ÖÂ∫¶ÂäüÂæ∑'],
      chaoduType: '',
      relationOptions: [
        'ÂÖàÊõæÁ•ñÁà∂', 'ÂÖàÊõæÁ•ñÊØç', 'ÂÖàÁ•ñÁà∂', 'ÂÖàÁ•ñÊØç', 'Â§ñÁ•ñÁà∂', 'Â§ñÁ•ñÊØç',
        'ÂÖàÁà∂', 'ÂÖàÊØç', 'Â≤≥Áà∂', 'Â≤≥ÊØç', 'ÂÖàÂ§´', 'ÂÖàÂ¶ª', 'ÊïÖÁà±Â≠ê', 'ÊïÖÁà±Â•≥', 'Â•ΩÂèã', 'ÊÅ©Â∏à', 'ÂÖ∂‰ªñ'
      ],
      deceasedList: [this.getEmptyDeceased()],
      showGoods: false,
      goodsList: [],
      showReceiver: false, // ÈªòËÆ§‰∏çÊòæÁ§∫
      receiver: {
        name: '',
        phone: '',
        address: ''
      },
      genderOptions: ['Áî∑', 'Â•≥'],
      birthTimeOptions: ['Â≠êÊó∂', '‰∏ëÊó∂', 'ÂØÖÊó∂', 'ÂçØÊó∂', 'Ëæ∞Êó∂', 'Â∑≥Êó∂', 'ÂçàÊó∂', 'Êú™Êó∂', 'Áî≥Êó∂', 'ÈÖâÊó∂', 'ÊàåÊó∂', '‰∫•Êó∂'],
      showHistoryDialog: false,
      historyParticipants: [],
      historySelectIndex: -1,
      historySearch: '',
      hoverIdx: -1,
      batchDeleteMode: false,
      batchDeleteSet: new Set(),
      showImportInput: false,
    }
  },
  computed: {
    goodsTotalPrice() {
      return this.goodsList.reduce((sum, item) => {
        const price = Number(item.price) || 0
        const qty = Number(item.qty) || 0
        return sum + price * qty
      }, 0)
    },
    totalPrice() {
      const basePrice = this.selectedProjectIndex >= 0 ? this.fahuiProjects[this.selectedProjectIndex].price : 0
      const applicantCount = this.applicants.length
      return basePrice * applicantCount + this.goodsTotalPrice
    },
    totalPriceFormatted() {
      return this.totalPrice.toFixed(2) // ‰øùÁïô‰∏§‰ΩçÂ∞èÊï∞
    },
    filteredHistory() {
      if (!this.historySearch) return this.historyParticipants
      const kw = this.historySearch.trim()
      return this.historyParticipants.filter(item =>
        item.name.includes(kw) || (item.phone && item.phone.includes(kw))
      )
    }
  },
  methods: {
    showError(msg) {
      uni.showToast({ title: msg, icon: 'none' })
      return false
    },
    async loadBanner() {
      try {
        const res = await uniCloud.callFunction({
          name: 'getFahuiBanners',
          data: { t: Date.now() + Math.random() }
        })
        console.log('‰∫ëÂáΩÊï∞ËøîÂõûÔºö', res)
        if (res.result && res.result.data && res.result.data.length > 0) {
          let image = res.result.data[0].image
          let desc = res.result.data[0].description || ''
          // fileID ËΩ¨‰∏¥Êó∂ÈìæÊé•
          if (image && !/^https?:\/\//.test(image)) {
            const tempRes = await uniCloud.getTempFileURL({ fileList: [image] })
            if (tempRes.fileList && tempRes.fileList[0] && tempRes.fileList[0].tempFileURL) {
              image = tempRes.fileList[0].tempFileURL
            }
          }
          this.bannerImage = image
          this.bannerDesc = desc
          console.log('ÊúÄÁªàÊòæÁ§∫ÂõæÁâáÔºö', image)
          console.log('ÊúÄÁªàÊòæÁ§∫ÊèèËø∞Ôºö', desc)
        } else {
          this.bannerImage = ''
          this.bannerDesc = ''
        }
      } catch (e) {
        this.bannerImage = ''
        this.bannerDesc = ''
        console.error('Âä†ËΩΩÂ§¥ÂõæÂ§±Ë¥•', e)
      }
    },
    async loadSpecialIntro() {
      try {
        const res = await uniCloud.callFunction({ name: 'getFahuiSpecialIntro' })
        if (res.result && res.result.data) {
          this.specialIntro = res.result.data
        } else {
          this.specialIntro = { content: '', image: '' }
        }
      } catch (e) {
        this.specialIntro = { content: '', image: '' }
      }
    },
    async loadFahuiProjects() {
      try {
        const res = await uniCloud.callFunction({ 
          name: 'getFahuiProjects',
          data: { type: 'special' }
        })
        this.fahuiProjects = res.result && res.result.data ? res.result.data.map(p => ({
          ...p,
          price: Number(p.price) || 0
        })) : []
        // ÊÅ¢Â§ç‰∏äÊ¨°ÈÄâ‰∏≠
        const last = uni.getStorageSync('fahuiForm')
        if (last && last.fahuiProject) {
          const idx = this.fahuiProjects.findIndex(p => p._id === last.fahuiProject._id)
          this.selectedProjectIndex = idx >= 0 ? idx : -1
        } else {
          this.selectedProjectIndex = -1
        }
      } catch (e) {
        this.fahuiProjects = []
        uni.showToast({ title: 'Ëé∑ÂèñÊ≥ï‰ºöÈ°πÁõÆÂ§±Ë¥•', icon: 'none' })
      }
    },
    async loadGoodsConfig() {
      try {
        const result = await uniCloud.callFunction({ name: 'getFahuiGoodsConfig' });
        if (result.result && result.result.data) {
          this.showGoods = !!result.result.data.enabled && Array.isArray(result.result.data.goods) && result.result.data.goods.length > 0;
          this.goodsList = Array.isArray(result.result.data.goods)
            ? result.result.data.goods.filter(g => g.enabled).map(g => ({ ...g, qty: Number(g.qty) || 0 }))
            : [];
        } else {
          this.showGoods = false;
          this.goodsList = [];
        }
      } catch (error) {
        this.showGoods = false;
        this.goodsList = [];
        console.error('Âä†ËΩΩ‰ª£ÂäûÁâ©ÂìÅÈÖçÁΩÆÂ§±Ë¥•:', error);
      }
    },
    async getReceiverConfig() {
      try {
        const res = await uniCloud.callFunction({ name: 'getFahuiReceiverConfig', data: { t: Date.now() } })
        // ÂÖºÂÆπ enabled Â≠óÊÆµÂú® result ÊàñÊ†πÂØπË±°
        const enabled = (res.result && typeof res.result.enabled !== 'undefined' ? res.result.enabled : res.enabled)
        this.showReceiver = !!enabled
        console.log('[Ë∞ÉËØï] getFahuiReceiverConfig ËøîÂõû:', res, 'ÊúÄÁªà showReceiver:', this.showReceiver)
      } catch (e) {
        this.showReceiver = false
        console.log('[Ë∞ÉËØï] getFahuiReceiverConfig ÂºÇÂ∏∏:', e)
      }
    },
    getEmptyApplicant() {
      return {
        name: '',
        gender: '',
        phone: '',
        lunarBirthday: '',
        birthTime: '',
        address: ''
      }
    },
    getEmptyDeceased() {
      return {
        name: '',
        relation: '',
        lunarBirthday: '',
        deathDate: ''
      }
    },
    onParticipantsChange(participants) {
      // Âè™Âú®Êï∞ÊçÆÂèòÂåñÊó∂ËµãÂÄºÔºåÈÅøÂÖçÊ∑±Â∫¶ÂìçÂ∫îÂºèÂç°È°ø
      if (JSON.stringify(this.applicants) !== JSON.stringify(participants)) {
        this.applicants = participants.slice ? participants.slice() : participants
      }
    },

    selectProject(index) {
      console.log('ÈÄâ‰∏≠È°πÁõÆindex:', index)
      this.selectedProjectIndex = index
    },
    onSpouseGenderChange(e) {
      this.spouse.gender = this.genderOptions[e.detail.value]
    },
    onSpouseBirthdayChange(e) {
      this.spouse.lunarBirthday = e.detail.value
    },
    onSpouseBirthTimeChange(e) {
      this.spouse.birthTime = this.birthTimeOptions[e.detail.value]
    },
    onChaoduTypeChange(e) {
      this.chaoduType = this.chaoduTypes[e.detail.value]
    },
    addDeceased() {
      if (this.deceasedList.length < 5) {
        this.deceasedList.push(this.getEmptyDeceased())
      }
    },
    removeDeceased(index) {
      if (this.deceasedList.length > 1) {
        this.deceasedList.splice(index, 1)
      }
    },
    onRelationChange(index, e) {
      this.deceasedList[index].relation = this.relationOptions[e.detail.value]
    },
    onDeceasedBirthdayChange(index, e) {
      this.deceasedList[index].lunarBirthday = e.detail.value
    },
    onDeceasedDeathChange(index, e) {
      this.deceasedList[index].deathDate = e.detail.value
    },
    increaseGoodsQty(index) {
      this.goodsList[index].qty++
    },
    decreaseGoodsQty(index) {
      if (this.goodsList[index].qty > 0) {
        this.goodsList[index].qty--
      }
    },
    // ÂéÜÂè≤Êä•Âêç‰∫∫ÂºπÁ™ó
    openHistoryDialog(index) {
      this.historySelectIndex = index
      this.historyParticipants = uni.getStorageSync('historyParticipants') || []
      this.showHistoryDialog = true
    },
    selectHistoryParticipant(item) {
      if (this.historySelectIndex >= 0) {
        this.$set(this.applicants, this.historySelectIndex, { ...item })
      }
      this.showHistoryDialog = false
      this.historySelectIndex = -1
    },
    // Êä•ÂêçÊàêÂäüÂêé‰øùÂ≠òÂéÜÂè≤Êä•Âêç‰∫∫
    saveHistoryParticipants() {
      let history = uni.getStorageSync('historyParticipants') || []
      this.applicants.forEach(p => {
        if (!history.some(h => h.name === p.name && h.phone === p.phone)) {
          history.push({ ...p })
        }
      })
      // ÊúÄÂ§ö‰øùÂ≠ò20Êù°
      if (history.length > 20) history = history.slice(-20)
      uni.setStorageSync('historyParticipants', history)
    },
    // Â¢ûÂº∫Ê†°È™åÔºöÂßìÂêç„ÄÅÊâãÊú∫Âè∑‰∏çËÉΩÈáçÂ§ç
    validate() {
      // Ê†°È™åÊä•Âêç‰∫∫Êï∞Èáè
      if (!this.applicants || this.applicants.length === 0) {
        return this.showError('ËØ∑Ê∑ªÂä†Ëá≥Â∞ë‰∏Ä‰ΩçÊä•Âêç‰∫∫')
      }
      if (this.applicants.length > 5) {
        return this.showError('ÊúÄÂ§öÊ∑ªÂä†5‰ΩçÊä•Âêç‰∫∫')
      }
      // Ê†°È™åÊä•Âêç‰∫∫‰ø°ÊÅØ
      const nameSet = new Set()
      const phoneSet = new Set()
      for (let i = 0; i < this.applicants.length; i++) {
        const p = this.applicants[i]
        if (!p.name) return this.showError(`ËØ∑ËæìÂÖ•Á¨¨${i+1}‰ΩçÊä•Âêç‰∫∫ÂßìÂêç`)
        if (!p.gender) return this.showError(`ËØ∑ÈÄâÊã©Á¨¨${i+1}‰ΩçÊä•Âêç‰∫∫ÊÄßÂà´`)
        if (!p.phone) return this.showError(`ËØ∑ËæìÂÖ•Á¨¨${i+1}‰ΩçÊä•Âêç‰∫∫ÁîµËØù`)
        if (!/^1[3-9]\d{9}$/.test(p.phone)) return this.showError(`Á¨¨${i+1}‰ΩçÊä•Âêç‰∫∫ÁîµËØùÊ†ºÂºè‰∏çÊ≠£Á°Æ`)
        if (!p.lunarBirthday) return this.showError(`ËØ∑ÈÄâÊã©Á¨¨${i+1}‰ΩçÊä•Âêç‰∫∫ÂÜúÂéÜÁîüÊó•`)
        if (!p.birthTime) return this.showError(`ËØ∑ÈÄâÊã©Á¨¨${i+1}‰ΩçÊä•Âêç‰∫∫Âá∫ÁîüÊó∂Ëæ∞`)
        if (!p.address) return this.showError(`ËØ∑ËæìÂÖ•Á¨¨${i+1}‰ΩçÊä•Âêç‰∫∫Â∏∏‰ΩèÂú∞ÂùÄ`)
        if (nameSet.has(p.name)) return this.showError(`Á¨¨${i+1}‰ΩçÊä•Âêç‰∫∫ÂßìÂêçÈáçÂ§ç`)
        if (phoneSet.has(p.phone)) return this.showError(`Á¨¨${i+1}‰ΩçÊä•Âêç‰∫∫ÁîµËØùÈáçÂ§ç`)
        nameSet.add(p.name)
        phoneSet.add(p.phone)
      }
      // Ê†°È™åÊ≥ï‰ºöÈ°πÁõÆ
      if (this.selectedProjectIndex === -1) {
        return this.showError('ËØ∑ÈÄâÊã©Ê≥ï‰ºöÈ°πÁõÆ')
      }
      // Ê†°È™åË∂ÖÂ∫¶Ê≥ï‰ºö
      if (this.selectedProjectIndex === 1) {
        if (!this.chaoduType) return this.showError('ËØ∑ÈÄâÊã©Ë∂ÖÂ∫¶Á±ªÂûã')
        if (!this.deceasedList || this.deceasedList.length === 0) return this.showError('ËØ∑Ê∑ªÂä†Ëá≥Â∞ë‰∏Ä‰ΩçÂ∑≤ÊïÖ‰∫≤‰∫∫')
        for (let i = 0; i < this.deceasedList.length; i++) {
          const d = this.deceasedList[i]
          if (!d.name) return this.showError(`ËØ∑ËæìÂÖ•Á¨¨${i+1}‰ΩçÂ∑≤ÊïÖ‰∫≤‰∫∫ÂßìÂêç`)
          if (!d.relation) return this.showError(`ËØ∑ÈÄâÊã©Á¨¨${i+1}‰ΩçÂ∑≤ÊïÖ‰∫≤‰∫∫ÂÖ≥Á≥ª`)
        }
      }
      // Ê†°È™åÊî∂‰ª∂‰ø°ÊÅØÔºàÂ¶ÇÊòæÁ§∫Êó∂ÔºåÂÖ®ÈÉ®Â°´ÂÜôÊàñÂÖ®ÈÉ®‰∏∫Á©∫Ôºâ
      if (this.showReceiver) {
        const { name, phone, address } = this.receiver
        const filled = name || phone || address
        const allFilled = name && phone && address
        if (filled && !allFilled) return this.showError('ËØ∑ÂÆåÊï¥Â°´ÂÜôÊî∂‰ª∂‰∫∫„ÄÅÁîµËØùÂíåÂú∞ÂùÄ')
        if (phone && !/^1[3-9]\d{9}$/.test(phone)) return this.showError('Êî∂‰ª∂‰∫∫ÁîµËØùÊ†ºÂºè‰∏çÊ≠£Á°Æ')
      }
      return true
    },
    nextStep() {
      if (!this.validate()) return

      // ÊûÑÂª∫Ë°®ÂçïÊï∞ÊçÆ
      const formData = {
        applicants: this.applicants,
        fahuiProject: {
          ...this.fahuiProjects[this.selectedProjectIndex],
          price: Number(this.fahuiProjects[this.selectedProjectIndex].price) || 0
        },
        spouse: this.selectedProjectIndex === 0 ? this.spouse : null,
        chaoduType: this.selectedProjectIndex === 1 ? this.chaoduType : null,
        deceasedList: this.selectedProjectIndex === 1 ? this.deceasedList : null,
        goods: this.goodsList.filter(item => item.qty > 0),
        receiver: this.receiver,
        totalPrice: this.totalPrice,
        timestamp: new Date().getTime()
      }

      // Êî∂‰ª∂‰ø°ÊÅØËá™Âä®‰øùÂ≠òÂà∞Êï∞ÊçÆÂ∫ì
      if (this.showReceiver && this.receiver.name && this.receiver.phone && this.receiver.address) {
        uniCloud.callFunction({
          name: 'addFahuiReceiver',
          data: { ...this.receiver }
        })
      }

      // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
      uni.setStorageSync('fahuiForm', formData)
      this.saveHistoryParticipants()

      // Ë∑≥ËΩ¨Âà∞Á°ÆËÆ§È°µ
      uni.navigateTo({
        url: '/pages/fahui/confirm',
        success: () => {
          console.log('Ë∑≥ËΩ¨Âà∞‰∏ìÂú∫Ê≥ï‰ºöÁ°ÆËÆ§È°µÊàêÂäü')
        },
        fail: (err) => {
          console.error('Ë∑≥ËΩ¨Â§±Ë¥•:', err)
          uni.showToast({ title: 'È°µÈù¢Ë∑≥ËΩ¨Â§±Ë¥•', icon: 'none' })
        }
      })
    },
    showProjectIntro(project) {
      uni.showModal({
        title: project.name + 'ÁÆÄ‰ªã',
        content: project.description || 'ÊöÇÊó†ÁÆÄ‰ªã'
      })
    },
    onHistorySearch() {
      this.hoverIdx = -1
    },
    clearHistorySearch() {
      this.historySearch = ''
    },
    deleteHistoryParticipant(idx) {
      const item = this.filteredHistory[idx]
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: `Á°ÆÂÆöË¶ÅÂà†Èô§‚Äú${item.name}‚ÄùÁöÑÂéÜÂè≤‰ø°ÊÅØÂêóÔºü`,
        success: (res) => {
          if (res.confirm) {
            // ‰ªéÂéüÂßãÊï∞ÁªÑÂà†Èô§
            const realIdx = this.historyParticipants.findIndex(h => h.name === item.name && h.phone === item.phone)
            if (realIdx !== -1) {
              this.historyParticipants.splice(realIdx, 1)
              uni.setStorageSync('historyParticipants', this.historyParticipants)
            }
          }
        }
      })
    },
    onHistoryKeydown(e) {
      if (!this.showHistoryDialog) return
      if (e.key === 'ArrowDown') {
        if (this.filteredHistory.length) {
          this.hoverIdx = (this.hoverIdx + 1) % this.filteredHistory.length
        }
        e.preventDefault()
      } else if (e.key === 'ArrowUp') {
        if (this.filteredHistory.length) {
          this.hoverIdx = (this.hoverIdx - 1 + this.filteredHistory.length) % this.filteredHistory.length
        }
        e.preventDefault()
      } else if (e.key === 'Enter') {
        if (this.hoverIdx >= 0 && this.hoverIdx < this.filteredHistory.length && !this.batchDeleteMode) {
          this.selectHistoryParticipant(this.filteredHistory[this.hoverIdx])
        }
      } else if (e.key === 'Escape') {
        this.showHistoryDialog = false
      }
    },
    onHistoryItemClick(idx, item) {
      if (this.batchDeleteMode) {
        this.toggleBatchDelete(idx)
      } else {
        this.selectHistoryParticipant(item)
      }
    },
    toggleBatchDeleteMode() {
      this.batchDeleteMode = !this.batchDeleteMode
      this.batchDeleteSet = new Set()
    },
    toggleBatchDelete(idx) {
      if (this.batchDeleteSet.has(idx)) {
        this.batchDeleteSet.delete(idx)
      } else {
        this.batchDeleteSet.add(idx)
      }
    },
    confirmBatchDelete() {
      if (!this.batchDeleteSet.size) return
      uni.showModal({
        title: 'ÊâπÈáèÂà†Èô§',
        content: `Á°ÆÂÆöË¶ÅÂà†Èô§ÈÄâ‰∏≠ÁöÑ${this.batchDeleteSet.size}Êù°ÂéÜÂè≤Êä•Âêç‰∫∫ÂêóÔºü`,
        success: (res) => {
          if (res.confirm) {
            const toDelete = Array.from(this.batchDeleteSet).map(i => this.filteredHistory[i])
            this.historyParticipants = this.historyParticipants.filter(h => !toDelete.some(d => d.name === h.name && d.phone === h.phone))
            uni.setStorageSync('historyParticipants', this.historyParticipants)
            this.batchDeleteSet = new Set()
            this.batchDeleteMode = false
          }
        }
      })
    },
    exportHistory() {
      const data = JSON.stringify(this.historyParticipants, null, 2)
      const blob = new Blob([data], { type: 'application/json' })
      const url = URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.href = url
      a.download = 'historyParticipants.json'
      a.click()
      URL.revokeObjectURL(url)
    },
    importHistory() {
      // Ëß¶ÂèëÊñá‰ª∂ÈÄâÊã©
      this.showImportInput = true
      this.$nextTick(() => {
        this.$refs.importFile && this.$refs.importFile.click()
      })
    },
    onImportFile(e) {
      const file = e.target.files[0]
      if (!file) return
      const reader = new FileReader()
      reader.onload = (evt) => {
        try {
          const arr = JSON.parse(evt.target.result)
          if (Array.isArray(arr)) {
            // ÂêàÂπ∂ÂéªÈáç
            arr.forEach(p => {
              if (!this.historyParticipants.some(h => h.name === p.name && h.phone === p.phone)) {
                this.historyParticipants.push(p)
              }
            })
            uni.setStorageSync('historyParticipants', this.historyParticipants)
            uni.showToast({ title: 'ÂØºÂÖ•ÊàêÂäü', icon: 'success' })
          } else {
            uni.showToast({ title: 'Ê†ºÂºèÈîôËØØ', icon: 'none' })
          }
        } catch {
          uni.showToast({ title: 'Ëß£ÊûêÂ§±Ë¥•', icon: 'none' })
        }
      }
      reader.readAsText(file)
      this.showImportInput = false
    },
  },
  onLoad() {
    this.getReceiverConfig();
    this.loadBanner()
    this.loadSpecialIntro()
    this.loadFahuiProjects() // Êñ∞Â¢û
    this.loadGoodsConfig();
    // Ëá™Âä®Â°´ÂÖÖ‰∏äÊ¨°Â°´ÂÜô
    const last = uni.getStorageSync('fahuiForm')
    if (last) {
      this.applicants = last.applicants || [this.getEmptyApplicant()]
      this.selectedProjectIndex = this.fahuiProjects.findIndex(p => p.id === last.fahuiProject?.id)
      this.spouse = last.spouse || this.spouse
      this.chaoduType = last.chaoduType || ''
      this.deceasedList = last.deceasedList || [this.getEmptyDeceased()]
      this.receiver = last.receiver || this.receiver
      
      // ÊÅ¢Â§çÂïÜÂìÅÈÄâÊã©
      if (last.goods) {
        last.goods.forEach(selected => {
          const index = this.goodsList.findIndex(item => item.id === selected.id)
          if (index !== -1) {
            this.goodsList[index].qty = selected.qty
          }
        })
      }
    }
  },
  onShow() {
    this.getReceiverConfig();
    try {
      uni.removeStorageSync('fahuiBannerCache')
    } catch (e) {}
    this.loadBanner()
    this.loadFahuiProjects() // Êñ∞Â¢ûÔºåÁ°Æ‰øùÊØèÊ¨°ËøõÂÖ•ÈÉΩÂà∑Êñ∞
    this.loadGoodsConfig();
  }
}
</script>

